.hero-img-holder {
    width: 100%;
    height: 100vh;
    position: relative;
}

.hero-img {
    width: 100%;
    object-fit: cover;
    height: 100%;
    background-size: cover;
    background-position: center;

}

.hero-img-holder:after {
    content: '';
    background-color: black;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0.6;

}

.hero {
    width: 100%;
    height: 100%;
    position: relative;
}

.content {
    /*transform: -50%, -50%;*/
    position: absolute;
    width: 50%;
    top: 35%;
    left: 25%;
    bottom: 0;
    right: 0;
    text-align: center;
}

.btn {
    margin: 2rem 1rem;
    color: black;
    font-weight: 600;
    padding: 0.5rem 1.2rem;
    background-color: rgb(248, 217, 15);
    display: inline-block;
    border: 1px solid white;
    cursor: pointer;
}

.btn.trans {
    background-color: transparent;
    color: aliceblue;
}

.btn:hover {

    background-color: rgba(255, 255, 255, 0.2);
    color: azure;

}